      p {padding:15px 0 0;font-size:14px;color:$gray6;line-height:22px;font-weight:$regular;}
      .last_txt {
        max-width:max-content;
        p {
          /*overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:2;-webkit-box-orient:vertical;*/
          &.more_active {display:block;}
        }
      }
      &.models_long {
        .last_txt {
          p {
            overflow:hidden; text-overflow:ellipsis; display:-webkit-box; -webkit-line-clamp:2; -webkit-box-orient:vertical;
