      &:hover, &:focus {
        border-color:$gray5;
        &:after {@include transform(translate(-50%, -50%) rotate(90deg));}
      }
      &:after {
        width:10px; height:10px;content:"";display:block;position:absolute; top:50%; left:50%;
        background:url(../../images/common/plus_icon.png) 50% 50% no-repeat; background-size:cover;
        @include transition(all, .2s);
        @include transform(translate(-50%, -50%));
      }
    }
  }
  @include mob() {
    width:100%; padding:em(40) 0 0;
    .kind {
      font-size:em(22); padding:em(12) em(15) em(8); margin:em(8) em(3) 0 0; font-weight:$bold;
      @include borderRadius(em(50));
      &:first-of-type {margin:em(8) em(3) 0 0;}
    }
    .substance {
      top:em(-5);
      strong {font-size:em(42); line-height:em(48); font-weight:$bold;}
      .explain {font-size:em(28); line-height:em(38);font-weight:$regular;}
    }
    .img_item {
      min-width:195px; width:28%; display:flex; align-items:center; background-color:$white;
      @include borderRadius(em(10));
      img { width:100%; }
    }
    .row_list {
      width:100%; display:block; padding:em(40) em(30); margin:0;
      justify-content:space-between; position:relative; border-top:em(2) solid #e5e5e5;
      @include boxSizing;
      &:first-of-type { padding:0 em(30) em(45); border:none; }
      &.active {background-color:#edf5ff;}
      .product_thumb {
        max-width:inherit; justify-content:space-between;
        .check_btn_label {left:-35px;}
        .img_item {
          width:40.57%; min-width:inherit;
        }
        .substance {
          width:55.07%; padding:0;
          strong {padding:15px 0 0;}
          .explain {padding:16px 0 0;}
        }
      }
      .skill_list {
        width:100%; display:block;padding:em(25) 0 0; border-top:em(2) solid #e5e5e5; margin:em(40) 0 0;
        & > div {
          max-width:inherit;min-width:inherit;display:flex;
          justify-content:space-between; padding:em(15) 0;
        }
        span {
          min-width:60%; display:block; width:max-content; font-size:em(32);
          line-height:em(42); font-weight:$bold; color:$gray5;
        }
        p {
          text-align:right; min-width:40%; padding:0;font-size:em(32);
          line-height:em(42); color:#4d4e4f; font-weight:$bold;
        }
        .last_txt {
          min-width:100%; height:em(40); display:block; overflow:hidden; padding:em(15) 0 0;
          &.on {
            span {
              &:after {@include transform(rotate(0deg));}
            }
          }
          span {
            width:100%; position:relative; cursor:pointer;
            &:after {
              width:em(24); height:em(15);content:"";display:block;; position:absolute;
              right:em(10); top:50%; background:url(./../../images/common/models_arr.png) 50% 50% no-repeat;
              background-size:cover;
              @include transition(all, .3s);
              @include transform(translate(0,-50%));
              @include transform(rotate(180deg));
            }
          }
          p {
            margin:em(40) 0 0; padding:em(30);text-align:left;text-overflow:inherit;
            -webkit-box-orient:inherit; background-color:#f5f5f5; font-size:em(28);
            font-weight:$medium; line-height:em(42);
            @include borderRadius(em(10));
          }
        }
      }
      .more_btn {display:none !important;}
    }
  }
}

