                }
              }
              &.half_mobile {
                > div {
                  width:calc(50% - 0.3rem); margin:0;
                }
              }
            }
          }
        }
        .check_01 {margin:em(40) 0 0;}
        .list_item {
          width:100%; padding:em(56) em(40) em(48); border:em(2) solid $gray3; border-radius:em(16);
          > span {font-size:em(24); line-height:em(44); border-radius:em(22); padding:0 em(26); top:em(56); right:em(40);}
        }
        .name_area {
          p {line-height:em(48);}
          .name {font-size:em(32);}
          .country {font-size:em(28); margin:0 0 0 em(20); padding:0 0 0 em(26);}
        }
        .address_area {
          padding:em(16) 0 0;
          p {font-size:em(28); line-height:em(58);}
        }
      }
      .box_shipping {
        .title_wrap {width:100%}
      }
    }
    .detail_wrap {
      padding:em(60) 0 0; margin:em(60) 0 0;
      .box {
        padding:em(60) 0 0; margin:em(60) 0 0;
        .form_check_radio_type01 ul li:nth-of-type(1), .form_check_radio_type01 ul li:nth-of-type(2){margin-top:0;}
        .select_only {max-width:100%;}
        .selected_info_wrap {
          margin:em(40) 0 0;
          .form_list {
            li {margin:em(12) 0 0;}
          }
          .info_list {
            margin:em(40) 0 0;
            li {
              font-size:em(28); line-height:em(34); margin:em(10) 0 0; padding:0 0 0 em(16);
            }
          }
        }
      }
      .installation_box {
        .box_s {
          margin:em(100) 0 0; border-radius:em(30);
          .title {
            padding:em(40) em(30); border-radius:em(30) em(30) 0 0;
            strong {font-size:em(34);}
            .btn_wrap {
