@charset "UTF-8";

// NHA-COMMON-SEARCH_RESULTS-055
.search_wrap {
    .content {margin:402px auto 0;}
    .search_row {
        width:100%; position:fixed; top:180px; background-color:$white; z-index:1;
        .input_wrap {
            width:100%; border-bottom:1px solid $gray2;
            .input_item {
                width:600px; margin:0 auto; padding:21px 0; position:relative;
                .btn_search_delete {
                    width:19px;height:19px; position:absolute; right:65px; top:36px;display:block; text-indent:-9999px;
                    background:url(./../../images/common/btn_delete.png) 50% 50% no-repeat;background-size:cover;
                }
                .btn_search {
                    width:32px; height:32px;position:absolute;right:20px; top:29px; text-indent:-9999px;
                    background:url(./../../images/common/btn_search.png) 50% 50% no-repeat;background-size:cover;
                }
                input {
                    width:100%; border:1px solid $gray3;font-size:16px;font-weight:$regular;
                    padding:13px 110px 14px 20px;color:$gray7; line-height:21px;
                    @include boxSizing;
                    @include borderRadius(100px);
                }
            }
        }
        .search_view {
            width:1300px;padding:0 50px;margin:0 auto;
            @include boxSizing;
        }
        .title_box {
            width:100%;
            p {
                width:fit-content; padding:25px 0; font-weight:$medium;
                span { margin:0 5px 0 0; }
                &.default_title {
                    font-size:16px;color:$gray6;font-weight:$medium;padding:50px 0 20px;
                    span {color:$blue5;}
                }
            }
        }
        .tab_wrap {
            width:100%;
            border-bottom:1px solid $gray2; margin:0 auto;
            @include boxSizing;
            ul {@extend %clearfix;}
            li {margin:0 29px;float:left;}
            button {
                display:block;padding:12px 0;font-size:18px;color:$gray5; position:relative;
                @include transition(all, .2s);
                &:after {
                    width:0; height:2px; background-color:$blue5; content:"";display:block;
                    position:absolute; bottom:-1px;left:50%; opacity:0;
                    @include transition(all, .2s);
                    @include translate(-50%, 0);
                }
                &:hover, &:focus {
                    color:$gray7;
                    &:after {width:100%; opacity:1;}
                    span {color:$blue5;}
                }
                &.active {
                    color:$gray7; font-weight:$medium;
                    &:after {width:100%; opacity:1;}
                    span {color:$blue5;}
                }
            }
            span {
                font-size:14px;
            }
        }
    }
    .search_result_type {
        width:1300px; margin:0 auto; padding:0 50px;
        @include boxSizing;
    }
    .item_list {
        padding:25px 0;
        /*리테일 ATM, 파이넨셜 ATM*/
        li.retail_atm, li.financial_atm, {
            .item_list_type01 {
                padding:0;
                .row_list {
                    padding:25px 55px 25px 10px; margin:0;
                    .product_thumb {
                        .img_item {min-width:150px;width:20%;}
                    }
                }
            }
        }
        /*파트*/
        li.part {
            .item_list_type02 {
                padding:0;
                .row_list {
                    padding:25px 30px 25px 10px; margin:0; border:0;
                    .product_thumb {
                        .img_item {min-width:150px;width:20%;}
                    }
                    .more_btn {right:20px;top:130px;}
                }
            }
        }
        /*소프트웨어*/
        li.software {
            .item_list_type05 {
                padding:0;
                .row_list {
                    padding:25px 30px 25px 10px; margin:0; border:0;
                    .more_btn {right:20px;top:60px;}
                }
            }
        }
        /*파이넨셜 서비스*/
        li.professional {
            .item_list_type05 {
                padding:0;
                .row_list {
                    padding:65px 30px 25px 10px; margin:0; border:0;
                    .more_btn {right:20px;top:100px;}
                }
            }
        }
        /*매니지드 서비스*/
        li.managed {
            .item_list_type05 {
                padding:0;
                .row_list {
                    padding:25px 30px 25px 10px; margin:0; border:0;
                    .more_btn {right:20px;top:60px;}
                }
            }
        }
    }
    .no_search_found_cont {
        width:100%; height:600px;text-align:center; padding:280px 0 0;
        @include boxSizing;
        span {display:block;font-size:14px;color:$gray5;font-weight:$regular;line-height:32px;}
    }
    @include mob() {
        .content {margin:em(600) auto 0;}
        .search_row {
            top:em(256);
            .input_wrap {
                border-bottom:em(2) solid $gray2;
                .input_item {
                    width:100%; padding:em(30);
                    @include boxSizing;
                    .btn_search_delete {
                        width:em(64);height:em(64); right:em(125); top:em(48);
                        background:url(./../../images/common/btn_delete_m.png) 50% 50% no-repeat;background-size:cover;
                    }
                    .btn_search {
                        width:em(64); height:em(64); right:em(60); top:em(48);
                        background:url(./../../images/common/btn_search_m.png) 50% 50% no-repeat;background-size:cover;
                    }
                    input {
                        padding:em(32) em(160) em(28) em(40); line-height:em(30);
                        border:em(2) solid $gray3;font-size:em(28);font-weight:$medium;
                        @include borderRadius(em(100));
                    }
                }
            }
            .search_view {width:100%;padding:0;}
            .title_box {
                padding:0 0 0 em(30);
                p {
                    padding:em(50) 0; font-weight:$bold; line-height:em(45);
                    span { margin:0 em(5) 0 0; }
                    &.default_title {
                        font-size:em(32); padding:em(35) 0 em(15); font-weight:$bold;
                    }
                }
                &.title_only {padding:0;}
            }
            .tab_wrap {
                overflow-x:scroll; padding:0; border:0;
                ul {
                    width:max-content; padding:0 em(30);
                    border-bottom:em(2) solid $gray2;
                }
                li {
                    float:left; margin:0 em(25);
                    &:first-of-type { margin:0 em(25) 0 0; }
                }
                button {
                    padding:em(30) 0;font-size:em(32);font-weight:$bold;
                    &:after {height:em(4);bottom:em(-2); opacity:0;}
                    &.active {
                        font-weight:$bold;
                        &:after {opacity:1;}
                    }
                }
                span {font-size:em(25);}
            }
        }
        .search_result_type {width:100%; padding:0;}
        .item_list {
            padding:em(30) 0;
            /*리테일 ATM, 파이넨셜 ATM*/
            li.retail_atm, li.financial_atm, {
                .item_list_type01 {
                    .row_list {
                        padding:em(40) 0 em(20) em(30); border-bottom:none;
                        .product_thumb {
                            .img_item {min-width:initial;width:40%;}
                        }
                    }
                }
            }
            /*파트*/
            li.part {
                .item_list_type02 {
                    .row_list {
                        padding:em(30);
                        .product_thumb {
                            .img_item {min-width:initial;width:40%;}
                        }
                    }
                }
            }
            /*소프트웨어*/
            li.software {
                .item_list_type05 {
                    padding:0 em(20);
                    .row_list {
                        padding:0;border-top:em(2) solid $gray2;
                        //&:first-of-type { border:none; }
                    }
                }
            }
            /*파이넨셜 서비스*/
            li.professional {
                .item_list_type05 {
                    padding:0 em(20);
                    .row_list {
                        padding:0;border-top:em(2) solid $gray2;
                        //&:first-of-type { border:none; }
                    }
                }
            }
            /*매니지드 서비스*/
            li.managed {
                .item_list_type05 {
                    padding:0 em(20);
                    .row_list {
                        padding:0;border-top:em(2) solid $gray2;
                        //&:first-of-type { border:none; }
                    }
                }
            }
        }
        .no_search_found_cont {
            height:70vh; padding:30vh 0 0;
            span {font-size:em(25);}
        }
    }
}