.slide_wrap {overflow:hidden;}
.layout_wrap {
	max-width:1200px; margin:0 auto; box-sizing:border-box;
}

@include pc-small(){
	.pc_show{display:block;}
	.m_show{display:none;}
}
@include mob(){
	.pc_show{display:none;}
	.m_show{display:block;}
}

/* popup Style */
.popup {
	position:fixed; top:0; left:0; width:100%; height:100%; z-index:999; display:none; justify-content:center; align-items:center;
	.popup_wrap {
		width:100%; box-sizing:border-box;
	}
	.popup_content {
		position:relative; z-index:2; margin:0 auto; box-sizing:border-box;
	}
	.btn_popup_close {
		position:absolute; top:0; right:0; z-index:3;
		@include textBackground(50px, 50px);
		@include backgroundSoltC()
	}
	.dimmed {width:100%; height:100%; background:rgba(0, 0, 0, 0.5); position:absolute; top:0; left:0; z-index:1;}
}

/* scroll bar custom */
.scroll_bar_custom {
	padding:0 13px 0 0; overflow-y:scroll;
	/*height:100px;*/ // 높이값 설정
	@include boxSizing;
	/* 스크롤바 설정*/
	&::-webkit-scrollbar {
		width:6px;
	}
	/* 스크롤바 막대 설정*/
	&::-webkit-scrollbar-thumb {
		height:17%; background-color:$gray2;
		/* 스크롤바 둥글게 설정  */
		border-radius:10px;
	}
	/* 스크롤바 뒷 배경 설정*/
	&::-webkit-scrollbar-track {
		background-color:transparent; // 투명
	}
}

/* Form Style */
.form_text_type01 {
	div {position:relative;}
	label {
		color:$gray5; font-size:16px; position:absolute; top:50%; left:24px; pointer-events:none;
		@include transition(all, 0.3s);
		@include translate(0, -50%);
		&.required {
			&:before {content:"* "; color:$red;}
		}
	}
	input {
		font-size:16px; padding:17px 25px 18px; width:100%; display:block; border:1px solid $gray2; border-radius:10px; box-sizing:border-box; background-color:$white;
		&.validField {padding:27px 25px 8px; outline:none; border-color:$gray2;}
		&.validField:focus {border-color:$blue5;}
		&.validField ~ label {
			top:10px; font-size:12px;
			@include translate(0, 0);
		}
		&:disabled {background-color:$gray1;}
	}
	span {font-size:12px; padding:7px 24px 0; display:none;}
	.errorField {
		input {border-color:$red;}
		input:focus {border-color:$red;}
		& ~ span {color:$red; display:block;}
	}
	.missingField {
		input {border-color:$red;}
		input:focus {border-color:$red;}
		& ~ span {color:$red; display:block;}
	}
	@include mob(){
		label {font-size:em(32); left:em(28); margin:em(2) 0 0;}
		input {
			font-size:em(32); padding:em(39) em(28); border-radius:em(10);
			&.validField {padding:em(58) em(28) em(20);}
			&.validField ~ label {
				top:em(28); font-size:em(24); margin:0;
			}
		}
		span {font-size:em(24); padding:em(10) 0 0;}
	}
}

.form_select_type01 {
	position:relative;
	div {
		padding:29px 28px; width:100%; display:block;/*border:1px solid $gray2; border-radius:10px;*/ box-sizing:border-box; position:relative;
		&:after {
			width:13px; height:8px; content:""; background-image:url(../../images/common/select_arr_default.png); position:absolute; top:50%; right:20px;
			@include backgroundSoltC();
			@include translate(0, -50%);
			@include transition(transform, .3s);
		}
		label {
			color:$gray5; font-size:16px; position:absolute; top:50%; left:24px; pointer-events:none;
			@include transition(all, 0.3s);
			@include translate(0, -50%);
			&.required {
				&:before {content:"* "; color:$red;}
			}
			&.select {
				top:10px; font-size:12px;
				@include translate(0, 0);
			}
		}
		input {
			width:100%; height:100%; color:$gray7 !important; border:1px solid $gray2; border-radius:10px; padding:27px 25px 7px; display:block; font-size:16px; text-align:left; position:absolute; top:0; left:0; z-index:2; cursor:pointer; box-sizing:border-box;
			&:focus {border-color:$blue5; outline:none;}
		}
		ul {
			width:100%; padding:66px 25px 14px; position:absolute; top:0; left:0; border:1px solid $blue5; border-radius:10px; box-sizing:border-box; background-color:$white; display:none;
			li {
				margin:10px 0 0;
				&:first-child {margin:0;}
				button {width:100%; font-size:14px; color:$gray5; line-height:24px; text-align:left;}
			}
		}
	}
	span {font-size:12px; padding:7px 24px 0; display:none;}
	.validField {
		input {
			border-color:transparent;
			&:focus {border-color:transparent;}
		}
		ul {display:block;}
		&:after {transform:rotate(180deg) translateY(50%);}
	}
	.missingField {
		input {
			border-color:$red;
			&:focus {border-color:$red;}
		}
		ul {border-color:$red;}
		~ span {color:$red; display:block;}
		&.validField {
			input {border-color:transparent}
		}
	}
	@include mob(){
		div {
			padding:em(60) em(28);
			&:after {
				width:em(24); height:em(15); top:50%; right:em(30); background-image:url(../../images/common/select_arr_default_m.png);
			}
			label {
				font-size:em(32); left:em(28); margin:em(2) 0 0;
				&.select {
					top:em(28); font-size:em(24); margin:0;
				}
			}
			input {
				padding:em(54) em(75) em(20) em(28); font-size:em(32); border-radius:em(10);
			}
			ul {
				padding:em(130) em(28) em(28); border-radius:em(10);
				li {
					margin:em(35) 0 0;
					button {font-size:em(32); line-height:em(42);}
				}
			}
		}
		span {font-size:em(24); padding:em(10) 0 0;}
		.vaildFiled {
			label {margin:0;}
		}
	}
}

.form_check_radio_default {
	position:relative;
	input[type="checkbox"] {
		position:absolute; top:0; left:0; z-index:-1; opacity:0;
		&:focus ~ label:before {border-color:$blue5;}
		&:hover ~ label:before {border-color:$blue5;}
		&:checked ~ label:before {border:none; background-color:$blue5;}
		&:checked ~ label:after {opacity:1;}
		& + label {
			display:inline-block; padding:0 0 0 32px; font-size:15px; color:$gray7; line-height:22px; position:relative;
			&:before {width:22px; height:22px; content:""; border:1px solid $gray5; border-radius:4px; box-sizing:border-box; background-color:$white; position:absolute; top:0; left:0;}
			&:after {
				width:22px; height:22px; content:""; display:block; position:absolute; top:0; left:0; opacity:0; background-image:url(../../images/common/check_icon.png);
				@include backgroundSoltC();
			}
		}
	}
	input[type="radio"] {
		position:absolute; top:0; left:0; z-index:-1; opacity:0;
		&:focus ~ label:before {border-color:$blue5;}
		&:hover ~ label:before {border-color:$blue5;}
		&:checked ~ label:before {border-color:$blue5;}
		&:checked ~ label:after {opacity:1;}
		& + label {
			display:inline-block; padding:0 0 0 32px; font-size:15px; color:$gray7; line-height:22px; position:relative;
			&:before {width:22px; height:22px; content:""; border:1px solid $gray5; border-radius:100%; box-sizing:border-box; background-color:$white; position:absolute; top:0; left:0;}
			&:after {
				width:16px; height:16px; border-radius:100%; content:""; display:block; position:absolute; top:3px; left:3px; opacity:0; background-color:$blue5;
			}
		}
	}

	@include mob(){
		input[type="checkbox"] {
			& + label {
				padding:0 0 0 em(57); font-size:em(28); line-height:em(44);
				&:before {width:em(44); height:em(44); border-radius:em(6);}
				&:after {
					width:em(44); height:em(44);
				}
			}
		}
		input[type="radio"] {
			& + label {
				padding:0 0 0 em(57); font-size:em(28); line-height:em(44);
				&:before {width:em(44); height:em(44);}
				&:after {
					width:em(32); height:em(32); left:em(6); top:em(6);
				}
			}
		}
	}
}

.form_check_radio_type01 {
	//margin:30px 0 0;
	.tit_wrap {
		@extend %clearfix;
		strong {float:left; font-size:20px; font-weight:$medium; color:$gray7;}
		.question_wrap {
			float:left; margin:3px 0 0 8px; position:relative; z-index:1;
			button {
				width:17px; height:17px; text-indent:-9999px; background-color:#cecece; background-image:url(../../images/common/question_icon.png);
				@include borderRadius(100%);
				@include transition(background-color, .2s);
				@include backgroundSolt;
				&:hover,
				&.active {background-color:$blue;}
			}
			.question_detail {
				width:288px; padding:14px 20px; position:absolute; bottom:40px; left:50%; margin:0 0 0 -144px; font-size:12px; color:$gray6; background-color:$white; display:none; text-align:center; line-height:20px;
				@include borderRadius(10px);
				@include boxSizing;
				@include opacity(0);
				@include boxShadow(2px 6px 8px rgba(15,13,13,.1));
				&:after {
					width:12px; height:12px; display:block; content:""; position:absolute; left:50%; bottom:-6px; border-bottom:1px solid transparent; border-right:1px solid transparent; background-color:$white; margin:0 0 0 -6px;
					@include boxSizing;
					@include rotate(45);
					@include boxShadow(2px 6px 16px rgba(15,13,13,.1));
				}
			}
		}
	}
	ul {
		padding:10px 0 0; font-size:16px; width:calc(100% + 8px); position:relative; left:-8px;
		@extend %clearfix;
		li {float:left; position:relative; margin:8px 0 0 8px;}

		/* 2개 일떄 */
		&.list_item01_2 {
			li {
				width:calc(50% - 8px);
				@media screen and (min-width: 1025px) {
					&:nth-of-type(1),&:nth-of-type(2) {margin-top:0;}
					&:nth-of-type(2n+1) {clear:both;}
				}
			}
		}

		/* 3개 일떄 */
		&.list_item01_3 {
			li {
				width:calc(33.333% - 8px);
				&:nth-of-type(1),&:nth-of-type(2) {margin-top:0;}
				@media screen and (min-width: 1025px) {
					&:nth-of-type(3) {margin-top:0;}
					&:nth-of-type(3n+1) {clear:both;}
				}
			}
		}

		/* 4개 일떄 */
		&.list_item01_4 {
			li {
				width:calc(25% - 8px);
				&:nth-of-type(1),&:nth-of-type(2) {margin-top:0;}
				@media screen and (min-width: 1025px) {
					&:nth-of-type(3),&:nth-of-type(4) {margin-top:0;}
					&:nth-of-type(4n+1) {clear:both;}
				}
			}
		}

		/* 사용안함 */
		/*
		li {width:246px; float:left; margin:8px 0 0 8px; position:relative;}
		&.list_item01 li:nth-of-type(2n+1) {margin:8px 0 0;}
		&.list_item02 li:nth-of-type(3n+1) {margin:8px 0 0;}
		*/
		input[type=radio],
		input[type=checkbox] {
			position:absolute; top:10px; left:10px;
			&:checked ~ label {font-weight:$bold; border-width:2px; border-color:$gray6;}
			&:focus,
			&:hover {
				& ~ label {border-color:$gray6;}
			}
			&:disabled ~ label {background-color:$gray1; color:$gray5;}
			&:disabled:hover ~ label {border-color:$gray2; cursor:default;}
			/*&.disabled_chk ~ label {background-color:$gray1;}*/
		}
		label {
			width:100%; height:58px; text-align:center; position:relative; background-color:$white; line-height:24px;
			@include displayFlexCenter;
			@include borderSet(1px, solid, $gray2);
			@include borderRadius(7px);
			@include boxSizing;
			@include transition(border-color, .2s);
		}
	}
	@include mob(){
		position:relative;
		.tit_wrap {
			strong {font-size:em(36); margin:0;}
			.question_wrap {
				margin:0 0 0 em(16); position:static;
				button {
					width:em(33); height:em(33); background-size:em(12) em(18);
				}
				.question_detail {
					width:calc(100vw - 10vw); padding:em(28) em(40); bottom:auto; top:em(64); left:0; margin:0; font-size:em(24); line-height:em(40); z-index:1;
					@include borderRadius(em(20));
					@include boxShadow(em(4) em(12) em(32) rgba(15,13,13,.1));
					&:after {display:none;}
				}
			}
		}
		ul {
			padding:em(22) 0 0; font-size:em(32); width:calc(100% + 0.7rem); left:em(-14);
			li {margin:em(14) 0 0 em(14);}

			/* 2개 일떄 */
			&.list_item01_2 {
				li {width:calc(50% - 0.7rem);}
			}

			/* 3개 일떄 */
			&.list_item01_3 {
				//li {width:calc(33.333% - 0.7rem);}
				li {width:calc(50% - 0.7rem);}
			}

			/* 4개 일떄 */
			&.list_item01_4 {
				//li {width:calc(25% - 0.7rem);}
				li {width:calc(50% - 0.7rem);}
			}

			input[type=radio],
			input[type=checkbox] {
				top:10px; left:10px;
			}
			label {
				height:em(120); line-height:em(40); border-radius:em(10);
			}
		}
	}
}

.form_check_radio_type02 {
	.notification_txt {margin:15px 0 0; padding:15px 20px; background-color:$gray1; font-size:16px; line-height:24px; color:$black;}
	ul {
		@extend %clearfix; width:100%; left:0;
		li {
			width:100%; float:none; margin:8px 0 0;
			&:first-child {margin:0;}
			label {height:auto; text-align:left; justify-content:left; padding:15px 20px;}
		}
	}
	@include mob(){
		.notification_txt {margin:em(25) 0 0; padding:em(25) em(30); font-size:em(32); line-height:em(40);}
		ul {
			li {
				margin:em(12) 0 0;
				label {padding:em(25) em(30);}
			}
		}
	}
}

.form_date_type01 {
	position:relative;
	label {display:block; text-indent:-9999px; height:0;}
	input[type=text] {
		width:100%; font-size:16px; line-height:56px; padding:0 60px 0 25px; border:1px solid $gray2; border-radius:10px; color:$gray7; background-color:$gray1; box-sizing:border-box;
		&:focus {border-color:$blue5; outline:none;}
	}
	.btn_calendar {
		position:absolute; top:50%; right:25px; background-image:url(../../images/common/calendar_icon.png);
		@include translate(0, -50%);
		@include textBackground(24px, 24px);
	}
	@include mob(){
		input[type=text] {
			font-size:em(32); line-height:em(116); padding:0 em(100) 0 em(28); border-width:em(2); border-radius:em(10);
		}
		.btn_calendar {
			width:em(64); height:em(64); background-size:cover; right:em(28);
		}
	}
}

.form_date_type02 {
	position:relative;
	label {
		color:$gray5; font-size:16px; position:absolute; top:50%; left:24px; pointer-events:none;
		@include transition(all, 0.3s);
		@include translate(0, -50%);
		&.required {
			&:before {content:"* "; color:$red;}
		}
	}
	input[type=text] {
		width:100%; font-size:16px; padding:17px 60px 18px 25px; border:1px solid $gray2; border-radius:10px; color:$gray7; box-sizing:border-box; background-color:$white;
		&:focus {border-color:$blue5; outline:none;}
		&.date_in {padding:27px 60px 8px 25px; outline:none; border-color:$gray2;}
		&.date_in ~ label {
			position:absolute; top:10px; font-size:12px;
			@include translate(0, 0);
		}
	}
	.btn_calendar {
		position:absolute; top:50%; right:25px; background-image:url(../../images/common/calendar_icon.png);
		@include translate(0, -50%);
		@include textBackground(24px, 24px);
	}
	@include mob(){
		label {font-size:em(32); left:em(28)}
		input[type=text] {
			font-size:em(32); padding:em(39) em(125) em(39) em(28); border-width:em(2); border-radius:em(10);
			&.date_in {padding:em(58) em(125) em(20) em(28);}
			&.date_in ~ label {
				top:em(28); font-size:em(24);
			}
		}
		.btn_calendar {
			width:em(64); height:em(64); background-size:cover; right:em(28);
		}
	}
}

.form_textarea_type01 {
	> div {position:relative;}
	label {
		color:$gray5; font-size:16px; position:absolute; top:24px; left:24px; pointer-events:none;
		@include transition(all, 0.3s);
		&.required {
			&:before {content:"* "; color:$red;}
		}
	}
	textarea {
		font-size:16px; padding:17px 25px; width:100%; display:block; border:1px solid $gray2; border-radius:10px; box-sizing:border-box; resize:none; background-color:$white;
		&.validField {padding:27px 25px 7px; outline:none; border-color:$gray2;}
		&.validField:focus {border-color:$blue5;}
		&.validField ~ .white_bg {display:block;}
		&.validField ~ label {
			top:10px; font-size:12px;
			@include translate(0, 0);
		}
	}
	.white_bg {width:98%; height:29px; background-color:$white; border-radius:10px 10px 0 0; position:absolute; top:1px; left:1px; z-index:0; display:none;}
	span {font-size:12px; padding:7px 24px 0; display:none;}
	.missingField {
		textarea {border-color:$red;}
		textarea:focus {border-color:$red;}
		& ~ span {color:$red; display:block;}
	}
	@include mob(){
		label {font-size:em(32); left:em(28); margin:em(2) 0 0;}
		textarea {
			font-size:em(32); padding:em(37) em(28); border-radius:em(10);
			&.validField {padding:em(54) em(28) em(20);}
			&.validField ~ label {
				top:em(28); font-size:em(24); margin:0;
			}
			.white_bg {height:em(56); border-radius:em(10) em(10) 0 0;}
		}
		span {font-size:em(24); padding:em(10) 0 0;}
	}
}

.form_tab_type01 {
	@extend %clearfix;
	.tit_wrap {
		margin:11px 0 0; float:left;
		strong {
			font-size:20px;padding:0 23px 0 0;position:relative; font-weight:$medium;
			button {
				position:absolute;display:block;width:14px; height:14px;margin:0 0 0 10px;right:0; top:53%; z-index:1;
				background:url(./../../images/common/more_i_btn.png) 50% 50% no-repeat;background-size:cover;
				@include transform(translate(0,-50%));
			}
		}
	}
	.tab_item {
		width:320px; float:right;
		@extend %clearfix;
		li {
			width:50%;position:relative;float:left;
			&:first-of-type {
				label {border-radius:7px 0 0 7px;}
			}
			&:last-child {
				left:-1px;
				label {border-radius:0 7px 7px 0;}
			}
		}
	}
	input[type=radio]{
		position:absolute; top:10px; left:10px;
		&:checked ~ label {background-color:$blue5; border-color:$blue5; color:$white;}
		&:focus,
		&:hover {
			& ~ label {border-color:$blue5;}
		}
		&:disabled ~ label {background-color:$gray1; cursor:default; color:$gray5;}
		&:disabled:hover ~ label {border-color:$gray2;}
		/*&.disabled_chk ~ label {background-color:$gray1;}
		&.disabled_chk:checked ~ label {background-color:$blue5; border-color:$blue5; color:$white;}*/
	}
	label {
		width:100%; height:46px; text-align:center; position:relative; background-color:$white; line-height:22px;
		font-size:14px;border:1px solid $gray2;color:$gray5;
		@include displayFlexCenter;
		@include boxSizing;
		@include transition();
	}
	@include mob (){
		.tit_wrap {
			margin:0;
			strong {
				padding:0 em(40) 0 0;font-size:em(36); margin:0;
				button {width:em(25); height:em(25);}
			}
		}
		.tab_item {
			width:100%; margin:em(25) 0 0;
			li {
				width:50%;
				&:first-of-type {
					label {border-radius:7px 0 0 7px;}
				}
				&:last-child {
					left:-1px;
					label {border-radius:0 7px 7px 0;}
				}
			}
		}
		label {height:em(100); line-height:em(40);font-size:em(32);border:em(2) solid $gray2;}
	}
}

.sub_title_wrap {
	width:100%; position:fixed; top:80px; left:0; z-index:3; text-align:center; background-color:$gray1;
	h2 {
		height:100px; font-size:36px; padding:32px 0; font-weight:$regular; color:$gray7; font-family:$bebasNeue;
		@include boxSizing;
	}
	.stepBystep {
		background-color:$gray7;
		ol {
			font-size:0; text-align:center; padding:28px 0;
			li {
				display:inline-block; padding:0 35px; margin:0 22px; position:relative;
				&:after {
					width:0; height:0;content:"";display:block;border-left:7px solid #4f4f4f;border-top:4px solid transparent;
					border-right:none;border-bottom:4px solid transparent;position:absolute; top:50%; right:-22px;
					@include boxSizing;
					@include translate(0, -50%);
				}
				&:last-child {
					margin:0;
					&:after {display:none;}
				}
				span { display:none; }
				strong {font-size:18px; line-height:24px; color:#787879; font-weight:$regular; display:inline-block;}
				&.ok {
					&:after {border-left:7px solid #c5c5c6;}
					strong {
						color:$gray2; position:relative; padding:0 0 0 30px;
						&:before {
							width:24px; height:24px; border:2px solid $gray2; border-radius:100%; box-sizing:border-box; position:absolute; top:0; left:0; background-image:url(../../images/common/step_icon.png); content:"";
							@include backgroundSoltC();
						}
					}
				}
				&.ing {
					&:after {border-left:7px solid $blue5;}
					strong {
						color:$white; font-weight:$bold; position:relative; padding:0 3px;
						&:after {width:100%; height:3px; position:absolute; bottom:-5px; left:0; background-color:$blue5; content:""}
					}
				}
			}
		}
	}
	@include mob(){
		top:em(124);
		h2 {
			height:em(132); font-size:em(60); padding:em(36) 0;
			@include boxSizing;
		}
		.stepBystep {
			ol {
				height:em(188); display:flex; padding:em(30) 0; justify-content:center;
				@include boxSizing;
				li {
					width:25%; padding:0 em(10); margin:0;
					&:nth-of-type(3) {
						min-width:em(170);
						strong { max-width:em(160);}
					}
					&:nth-of-type(4) {
						strong { max-width:em(160);}
					}
					&:after {border-left:em(12) solid #4f4f4f;border-top:em(7) solid transparent;border-bottom:em(7) solid transparent;right:em(-8); top:40%;}
					span {
						width:em(72); height:em(72); display:block; font-size:em(28); color:#666; font-weight:$bold;
						padding:em(18) 0; margin:0 auto; border:em(3) solid #666;
						@include boxSizing;
						@include borderRadius(em(72));
					}
					> div {
						min-height:em(60);
						display:flex;
						padding:em(5) 0 0;
						margin:0 auto;
						align-items:center;
					}
					strong {
						margin:0 auto; display:inline-block; font-weight:$medium; font-size:em(24); line-height:em(24);
						&:before {
							position:absolute; border:none; top:em(27); left:50%;
							@include transform(translate(-50%,0));
						}
					}
					&.ok {
						&:after {border-left:em(12) solid #c5c5c6;}
						span {
							text-indent:-9999px; position:relative; border-color:#c5c5c6;
							&:before {
								width:em(28); height:em(21); display:block; content:"";
								background:url(./../../images/common/stepBystep_active_icon.png) 50% 50% no-repeat;
								background-size:cover; position:absolute; top:50%; left:50%;
								@include transform(translate(-50%,-50%));
							}
						}
						strong {
							padding:0; color:$gray2; position:initial;
							&:before {display:none;}
						}
					}
					&.ing {
						&:after {border-left:em(12) solid $blue5;}
						&:before {background-color:$blue5;border-color:$blue5;}
						span {background-color:$blue5;border-color:$blue5;color:$white;}
						strong {
							padding:0;
							&:after {width:100%; height:em(4); bottom:em(-12);}
						}
					}
				}
			}
		}
	}
}

