#filters {
	width:288px; height:calc(100vh - 180px); position:fixed; top:180px; left:0; background-color:$white; z-index:2;
	@include boxShadow(0 0 24px rgba(15, 13, 13, .2));
	@include transition(transform, 0.5s);
	.filter_title {
		border-bottom:1px solid $gray2; padding:0 35px;
		.default_box {
			padding:27px 0 26px; position:relative;
			h3 {font-size:20px; font-weight:$medium; color:$gray7;}
			.btn_clearFilters {
				font-size:13px; line-height:24px; position:absolute; top:50%; right:0; font-weight:$medium; color:$gray7;
				&:after {width:100%; height:1px; position:absolute; bottom:3px; left:0; background-color:$gray7; content:"";}
				@include transition();
				@include translate(0, -50%);
				&:hover, &:focus {
					color:$blue5;
					&:after {background-color:$blue5;}
				}
			}
			/*.btn_apply {display:none;}*/
			.btn_close {display:none;}
		}
		.availability_box {
			border-top:1px solid $gray2; padding:28px 0;
			strong {font-size:20px; margin:0 0 20px; font-weight:$medium; color:$gray7; display:block;}
			p {
				display:flex; justify-content:space-between;
				button {
					width:49%; font-size:13px; line-height:36px; border-radius:30px; border:1px solid $gray3; color:$gray7; font-weight:$medium;
					@include transition();
					&:hover, &:focus {
						color:$white; background-color:$blue5 !important; border-color:$blue5 !important;
					}
					&.on {
						color:$white; background-color:$gray7; border-color:$gray7;
					}
				}
			}
		}
		&.filter_title_snb {display:none;}
	}
	.btn_filter {
		width:36px; height:36px; text-indent:-9999px; position:absolute; top:0; right:-36px; background-color:$gray7; border-radius:0 10px 10px 0;
		&:after {
			position:absolute; top:0; left:0; content:""; background-image:url(../../images/common/btn_filter.png);
			@include transition();
			@include textBackground(100%, 100%);
			@include backgroundSoltC();
		}
	}
	.filter_cont {
		width:100%; height:calc(100% - 74px); padding:0 30px; position:absolute; top:74px; left:0; overflow-y:auto; box-sizing:border-box;
		&.filter_cont_explore {height:calc(100% - 209px); top:209px;}
		&.filter_cont_snb {height:calc(100% - 10px); top:10px;}
		.filter_type {
			border-bottom:1px solid $gray2;
			&:last-child {border:none;}
			> strong {font-size:14px; font-weight:$medium; color:$gray7; display:block;}
		}
		.filter_type01 {
			padding:30px 0;
			strong {margin:0 0 20px;}
			li {
				margin:10px 0 0;
				&:first-child {margin:0;}
				.form_check_radio_default input[type="checkbox"] + label {font-size:14px; line-height:20px; padding:0 0 0 25px;}
				.form_check_radio_default input[type="checkbox"] + label:before {width:20px; height:20px;}
				.form_check_radio_default input[type="checkbox"] + label:after {width:20px; height:20px;}
			}
		}
		.filter_type02 {
			padding:20px 0 0;
			> ul {
				overflow:hidden; margin:0 0 5px;
				li {
					float:left; width:50%;
					a {
						font-size:14px; padding:20px 0 24px; text-align:center; font-weight:$medium; display:block; color:$gray7; opacity:.4; position:relative;
						&:after {width:100%; height:4px; background-color:$gray7; position:absolute; bottom:0; left:0; content:"";}
					}
					&.active a {
						color:$blue5; opacity:1;
						&:after {background-color:$blue5;}
					}
				}
			}
			> strong {
				font-size:14px; padding:4px 0; margin:5px 0 15px; color:$gray7; font-weight:$medium; display:block;
			}
			.product {border-top:4px solid $gray2; /*border-bottom:1px solid $gray2*/}
			.products {
				.product_cont {display:none;}
				.product_cont:first-child {display:block;}
			}
			.snb_s {
				border-top:1px solid $gray2;
				> a {
					font-size:14px; padding:20px 20px 20px 0; color:$gray7; font-weight:$medium; position:relative;
					&:after {
						width:17px; height:17px; margin:-8px 0 0; position:absolute; top:50%; right:0; content:""; background-image:url(../../images/common/snb_arr.png);
						@include backgroundSoltC();
						@include transition();
					}
					&.on:after {
						@include rotate(180);
					}
				}
				> ul {
					padding:10px 20px 30px;
					li {
						margin:10px 0 0; position:relative; padding:0 0 0 10px;
						&:before {width:2px; height:2px; background-color:$gray6; border-radius:100%; position:absolute; top:7px; left:0; content:"";}
						&:first-child {margin:0;}
						a {
							font-size:14px; color:$gray6; display:block;
							span {}
							em {}
							&:hover, &:focus, &.on {color:$blue5;}
						}
					}
				}
