@charset "UTF-8";

// NHA-COMMON-CART-010
.cart_form_type01 {
  max-width:1200px; margin:0 auto; padding:80px 0;
  .cart_tab_menu {
    padding:0 0 22px; border-bottom:1px solid rgba(0,0,0,.1); display:flex; justify-content:center; text-align:center;
    li {
      margin:0 0 0 80px; position:relative;
      &:first-child {margin:0;}
      button {
        font-size:16px; color:$gray5;
        &:after {
          width:0; height:2px; content:""; background-color:$blue5; position:absolute; bottom:-23px; left:50%;
          @include transition(width,.2s);
          @include translate(-50%, 0);
        }
        &:hover, &:focus {
          color:$gray7;
          &:after {width:100%;}
        }
        &.btn_on {
          color:$gray7; font-weight:$medium;
          &:after {width:100%;}
        }
      }
    }
  }
  .cart_tab_contents {
    padding:90px 0 0;
    .list_wrap {
      margin:92px 0 0;
      &:first-child {margin:0;}
    }
    .top_tit_wrap {
      position:relative; padding:0 0 30px; border-bottom:2px solid $gray6;
      strong {font-size:24px; color:$gray7; font-weight:$medium;}
      .btn_area {
        position:absolute; top:-8px; right:0;
        button {
          padding:0 20px; border:1px solid $gray3; border-radius:19px;
          span {
            font-size:13px; color:$gray7; font-weight:$medium; line-height:36px;
            @include transition(color,.2s);
          }
          &:hover {
            span {color:$blue5;}
          }
        }
        .remove_all {margin:0 0 0 4px;}
      }
    }
    .cart_wish_list {
      padding:20px 0; border-top:1px solid rgba(0,0,0,.1);
      &:nth-of-type(2){border-top:none;}
      .wish_list_wrap {
        padding:68px 60px 58px 70px; position:relative;
        @extend %clearfix;
        &.content_active {
          &:after {width:100%; height:100%; content:""; border:2px solid $gray7; display:block; position:absolute; top:0; left:0; z-index:-1; box-sizing:border-box;}
        }
      }
      .form_check_radio_default {position:absolute; top:20px; left:30px;}
      .prd_info_cart {
        &.parts_form {
          .prd_item {
            position:relative;
            > em {position:absolute; left:110px; bottom:10px; padding:0;}
          }
        }
        .prd_item {
          margin:50px 0 0; border-top:1px solid $gray2; padding:50px 0 0;
          &:first-child {margin:0; border:none; padding:0;}
          @extend %clearfix;
          .thumb {
            width:90px; float:left; background-color:$gray1; border-radius:8px;
            img {width:100%;}
          }
          .info {
            width:calc(100% - 110px); float:right;
            .info_title {
              float:left; position:relative;
              strong {font-size:32px; display:block; color:$gray7;}
              em {font-size:13px; color:$gray5; font-weight:$medium; background-color:$gray1; padding:0 12px; line-height:26px; border-radius:13px; box-sizing:border-box; position:absolute; top:42px; left:0; display:inline-block;}
            }
            .ordered_info_wrap {
              float:right;
              .increase_control_btn {
                margin:0; float:left;
                input {width:60px;}
              }
              .price_area {
                float:left; margin:0 0 0 90px; text-align:right;
                p {font-size:24px; color:$gray7; font-weight:$bold;}
                span {font-size:16px; color:$gray7; font-weight:$bold; display:block; margin:8px 0 0;}
              }
            }
          }
          .option_list {
            width:796px; padding:46px 0 0 20px; float:left;
            > li {
              border-bottom:1px solid $gray2; position:relative;
              &:first-child {border-top:1px solid $gray2;}
              > span {font-size:16px; color:$gray7; font-weight:$medium; position:absolute; top:22px; right:0;}
            }
            button {
              width:100%; text-align:left; padding:20px 0;
              span {
                font-size:16px; color:$gray5; font-weight:$medium; position:relative; padding:0 24px 0 0;
                &:after {
                  width:13px; height:8px; content:""; background-image:url(../../images/my_menu/order_more_btn.png); position:absolute; top:50%; right:0;
                  @include translate(0, -50%);
                  @include transition(transform, .2s);
                }
              }
              &.active {
                span {
                  &:after {transform:rotate(180deg) translateY(50%);}
                }
              }
              &.disabled {
                span {
                  transition:none;
                  &:after {opacity:.4; transform:rotate(0) translateY(-50%);}
                }
              }

            }
            .detail_option {
              margin:0 0 20px; padding:30px; display:none; background-color:$gray1;
              .first_option, .second_option {
                @extend %clearfix;
              }
              .first_option {margin:0 0 36px;}
              .second_option {
                .product_area {padding:40px 0 0; border-top:1px solid #dcdcdc;}
                .product_info_area {
                  padding:40px 0 0 40px; border-top:1px solid #dcdcdc;
                  span {padding:54px 0 0;}
                }
              }
              p {font-size:16px; color:$gray7; font-weight:$medium;}
              .product_area {
                width:200px; float:left;
                ul {padding:38px 0 0;}
                li {
                  margin:12px 0 0; padding:0 0 0 12px; font-size:14px; color:$gray5; position:relative;
                  &:first-child {margin:0;}
                  &:before {
                    content:"·"; position:absolute; top:50%; left:0;
                    @include translate(0, -50%);
                  }
                }
              }
              .product_info_area {
                width:calc(100% - 240px); padding:0 0 0 40px; float:left;
                > div {width:25%; float:left;}
                span {font-size:14px; color:$gray7; font-weight:$medium; display:block; padding:38px 0 0;}
              }
            }
            .list_form {
              dl {
                margin:32px 0 0;
                @extend %clearfix;
                &:first-child {margin:0;}
              }
              dt {width:280px; font-size:16px; color:$gray5; float:left;}
              dd {width:calc(100% - 280px); font-size:16px; color:$gray7; float:left; line-height:24px;}
            }
          }
          .remove_btn {
            margin:44px 0 0 10px; padding:0 20px; border:1px solid $gray3; border-radius:19px; float:right;
            span {
              font-size:13px; color:$gray7; font-weight:$bold; line-height:36px; padding:0 18px 0 0; position:relative;
              @include transition(color, .2s);
              &:after {
                width:12px; height:16px; background-image:url(../../images/my_menu/price_remove_ico.png); content:""; position:absolute; top:50%; right:0;
                @include translate(0, -50%);
              }
            }
            &:hover {
              span {color:$blue5;}
            }
          }
          > em {
            width:calc(100% - 110px); font-size:14px; color:#ea4335; padding:20px 0 0; float:right;
            &.blue {color:$blue5};
          }
        }
      }
    }
    .total_amount_view {
      padding:40px 0 80px; border-top:1px solid $gray3; border-bottom:1px solid $gray3; line-height:56px;
      @extend %clearfix;
      .amount_wrap {
        padding:0 0 0 20px; float:left;
        p {font-size:24px; color:$gray7; font-weight:$medium; float:left;}
        em {font-size:36px; color:$blue5; font-weight:$medium; float:left; margin:0 0 0 30px;}
      }
      .btn_wrap {
        float:right;
        button {width:230px; border-radius:8px; box-sizing:border-box;}
        span {font-size:18px; font-weight:$bold; line-height:56px;}
        .check_btn_01 {
          border:1px solid $gray3;
          span {
            color:$gray7;
            @include transition(color,.2s);
          }
          &:hover {
            span {color:$blue5;}
          }
        }
        .check_btn_02 {
          border:1px solid $blue5; background-color:$blue5;
          @include transition(background-color,.2s);
          span {color:$white;}
          &:hover {background-color:$blue6;}
        }
      }
    }
    .cart_wish_list_gap {
      text-align:right;
      p {font-size:14px; color:$gray5; padding:200px 0; border-bottom:1px solid rgba(0,0,0,.1); text-align:center;}
      a {
        margin:40px 0 0; padding:0 37px; border:1px solid $gray3; border-radius:8px; box-sizing:border-box; display:inline-block;
        span {
          font-size:18px; color:$gray7; font-weight:$bold; line-height:56px;
          @include transition(color,.2s);
        }
        &:hover {
          span {color:$blue5;}
        }
      }
    }
  }

  @include mob(){
    padding:em(70) 0 em(120); position:relative; overflow:hidden;
    .cart_tab_wrap {
      padding:0 em(30); overflow-x:scroll;
    }
    .cart_tab_menu {
      width:max-content; padding:0 0 em(30); border-bottom:em(2) solid rgba(0, 0, 0, .1); overflow:hidden;
      li {
        margin:0 0 0 em(40);
        button {
          font-size:em(30);
          &:after {height:em(4); bottom:em(-31);}
          &:hover, &:focus {
            font-weight:$bold;
            &:after {width:100%;}
          }
          &.btn_on {
            font-weight:$bold;
            &:after {width:100%;}
          }
        }
      }
    }
    .cart_tab_contents {
      padding:em(80) em(30) 0;
      .list_wrap {margin:em(80) 0 0;}
      .top_tit_wrap {
        padding:0 0 em(40); border-bottom:em(4) solid $gray6;
        strong {font-size:em(32); font-weight:$bold;}
        .btn_area {
          position:static; margin:em(20) 0 0;
          button {
            padding:0 em(30); border:em(2) solid $gray3; border-radius:em(36);
            span {
              font-size:em(24); font-weight:$bold; line-height:em(72);
            }
            &:hover {
              span {color:$gray7;}
            }
          }
          .remove_all {margin:0 0 0 em(4);}
        }
      }
      .cart_wish_list {
        padding:em(30) 0; border-top:em(2) solid rgba(0,0,0,.1);
        .wish_list_wrap {
          padding:em(100) em(20) em(80);
          &.content_active {
            &:after {border:em(2) solid $blue5; border-radius:em(8);}
          }
        }
        .form_check_radio_default {position:absolute; top:em(10); left:em(20);}
        .prd_info_cart {
          &.parts_form {
            .prd_item {
              position:static;
              > em {position:static; width:100%; padding:em(30) 0 0; text-align:right;}
            }
          }
          .prd_item {
            margin:em(60) 0 0; padding:em(60) 0 0;
            .thumb {width:em(150); border:em(2) solid $gray2; border-radius:em(8); background-color:$white; box-sizing:border-box;}
            .info {
              width:calc(100% - 9rem);
              .info_title {
                width:100%;
                strong {font-size:em(36); display:block;}
                em {font-size:em(24); margin:em(12) 0 0; position:static; padding:0 em(10); line-height:em(36);}
              }
              .ordered_info_wrap {
                width:100%; margin:em(20) 0 0; float:left;
                .increase_control_btn {
                  height:em(80); float:left;
                  input {width:em(50);}
                  .control_btn {width:em(50);}
                }
                .price_area {
                  float:right; margin:0;
                  p {font-size:em(32); color:$blue5;}
                  span {font-size:em(28); display:block; margin:em(12) 0 0;}
                }
              }
            }
            .option_list {
              width:100%; margin:em(36) 0 0; padding:em(26) 0 0; border-top:em(2) solid #e5e5e5;
              > li {
                margin:em(2) 0 0; padding:0; border-bottom:none;
                &:first-child {margin:0; border-top:none;}
                > span {font-size:em(28); font-weight:$bold; top:em(18); right:0;}
              }
              button {
                padding:em(18) 0;
                span {font-size:em(28); padding:0 em(36) 0 0;}
                &:after {
                  width:em(26); height:em(16); background-image:url(../../images/my_menu/order_more_btn_m.png); background-size:em(26) em(16);
                }
              }
              .detail_option {
                margin:0 0 em(30); padding:0; background-color:$white; border:em(2) solid $gray2;
                .first_option {
                  margin:0;
                  .product_area {border-top:none;}
                }
                .second_option {
                  margin:0; padding:em(80) 0 0; border-top:em(2) solid $gray2;
                  .product_area {padding:em(40) em(30); border-top:none;}
                  .product_info_area {
                    padding:em(40) em(30); border-top:none;
                    span {padding:0;}
                  }
                }
                p {font-size:em(32); float:left;}
                .product_area {
                  width:100%;  padding:em(40) em(30); box-sizing:border-box;
                  p {width:calc(50% - 4px);}
                  ul {width:calc(50% - 4px); padding:0; float:right;}
                  li {margin:em(12) 0 0; padding:0 0 0 em(24); font-size:em(28);}
                }
                .product_info_area {
                  width:100%; margin:em(72) 0 0; padding:em(40) em(30); box-sizing:border-box;  background-color:$gray1;
                  > div {width:100%; margin:em(30) 0 0;}
                  .material_code {margin:0;}
                  span {font-size:em(28); padding:0; float:right;}
                }
                .discount_option {
                  margin:0; padding:em(40) em(30); border-top:em(2) solid $gray2; background-color:$gray1; text-align:left;
                  p {
                    width:100%; margin:em(30) 0 0; font-size:em(28); float:none; position:relative;
                    &:first-child {margin:0;}
                    span {position:absolute; top:0; right:0;}
                  }
                }
              }
              .list_form {
                padding:em(40) em(30);
                dl {margin:em(30) 0 0;}
                dt {width:calc(50% - 4px); font-size:em(32);}
                dd {width:calc(50% - 4px); font-size:em(32); float:right; line-height:em(42)}
              }
            }
            .remove_btn {
              margin:0; padding:0 em(30); border:em(2) solid $gray3; border-radius:em(36); position:absolute; top:em(20); right:em(20);
              span {
                font-size:em(24); line-height:em(68); padding:0 em(36) 0 0;
                &:after {
                  width:em(24); height:em(36); background-size:em(24) em(36);
                }
              }
              &:hover {
                span {color:$gray7;}
              }
            }
            > em {width:100%; font-size:em(26); padding:em(30) 0 0;}
          }
        }
      }
      .total_amount_view {
        padding:em(30) 0 em(60); border-top:em(2) solid $gray3; border-bottom:em(2) solid $gray3; line-height:em(108);
        .amount_wrap {
          width:100%; padding:0;
          p {font-size:em(42); font-weight:$bold;;}
          em {font-size:em(48); font-weight:$bold; float:right; margin:0;}
        }
        .btn_wrap {
          width:100%; margin:em(20) 0 0;
          button {width:48%; border-radius:em(16);}
          span {font-size:em(26); line-height:em(108);}
          .check_btn_01 {
            border:em(2) solid $gray3;
            &:hover {
              span {color:$gray7;}
            }
          }
          .check_btn_02 {
            border:em(2) solid $blue5;
            &:hover {background-color:$blue5;}
          }
        }
      }
      .cart_wish_list_gap {
        p {font-size:em(26); border-bottom:em(2) solid rgba(0,0,0,.1); padding:em(200) 0;}
        a {
          margin:em(40) 0 0; padding:0 em(40); border:em(2) solid $gray3; border-radius:em(16);
          span {
            font-size:em(32); line-height:em(108);
          }
          &:hover {
            span {color:$gray7;}
          }
        }
      }
    }
  }
}