// NHA-COMMON-ORDER_TRACKING-022, NHA-COMMON-ORDER_DETAIL-023, NHA-COMMON-RMA_TRACKING-042
.order_form {
    max-width:1200px; margin:0 auto; padding:80px 0;
    .box {
        padding:80px 0; border-bottom:1px solid $gray2;
        &:last-of-type {border-bottom:none; padding:80px 0;
            > strong {margin:0;}
            .total_amount {padding:64px 0 0;}
        }
        &.bill_of_loading {padding:0 0 66px;}
        &.border_none {border-bottom:none;}
        &.padding_bottom {padding:0 0 80px;}
        > strong {display:block; font-size:26px; line-height:26px; margin:0 0 30px; font-weight:$medium; color:$gray7}
        .title {
            margin:0 0 50px; line-height:26px; position:relative;
            @extend %clearfix;
            strong {display:inline-block; vertical-align:middle; font-size:26px; line-height:26px; font-weight:$medium; color:$gray7; float:left;}
            > span {float:left; font-size:12px; color:$gray5; font-weight:$medium; line-height:22px; border-radius:11px; background-color:$gray1; margin:2px 0 0 30px; padding:0 12px;}
            p {float:left; font-size:14px; color:$gray5; margin:0 0 0 10px;}
            .btn_change {
                display:inline-block; margin:0 0 0 20px; vertical-align:middle; padding:0 20px; border:1px solid $gray2; border-radius:20px; position:relative; top:-5px;
                span {
                    display:block; font-size:15px; line-height:36px; padding:0 17px 0 0; color:$gray6; position:relative;
                    &:after {
                        width:16px; height:16px; content:""; position:absolute; top:50%; right:0; background-image:url(../../images/common/icon_arrow.png);
                        @include backgroundSoltC();
                        @include translate(0, -50%);
                    }
                }
                @include transition();
                &:hover {
                    span {
                        color:$blue5;
                        &:after {background-image:url(../../images/common/icon_arrow_on.png)}
                    }
                }
                &.btn_invoice {
                    span {
                        &:after {
                            width:11px; height:10px; background-image:url(../../images/common/excel_down_icon.png);
                            @include translate(0, -45%);
                        }
                    }
                    &:hover {
                        span {
                            &:after {background-image:url(../../images/common/download_blue_icon.png)}
                        }
                    }
                }
            }
            em {
                display:inline-block; padding:0 10px; margin:0 0 0 30px; line-height:22px; font-size:12px; color:$gray5; background-color:$gray1;
                @include borderRadius(15px);
            }
            .btn_order {
                display:block; border:1px solid $gray3; border-radius:19px; padding:0 20px; position:absolute; top:50%; right:0;
                @include translate(0, -50%);
                &:hover, &:focus {
                    span {
                        color:$blue5;
                        &:after {background-image:url(../../images/my_menu/order_arr_ico_hover.png);}
                    }
                }
                &.invoice {
                    &:hover, &:focus {
                        span {
                            &:after {background-image:url(../../images/my_menu/order_download_ico_hover.png);}
                        }
                    }
                    span {
                        &:after {width:11px; height:10px; background-image:url(../../images/my_menu/order_download_ico.png);}
                    }
                }
                &.choose_files {
                    &:hover, &:focus {
                        span {
                            &:after {background-image:url(../../images/my_menu/order_download_ico_hover.png);}
                        }
                    }
                    span {
                        &:after {width:11px; height:10px; background-image:url(../../images/my_menu/order_download_ico.png); transform:rotate(180deg) translateY(50%);}
                    }
                }
                &.disabled {
                    opacity:.4; cursor:default;
                    &:hover, &:focus {
                        span {
                            color:$gray7;
                            &:after {background-image:url(../../images/my_menu/order_download_ico.png);}
                        }
                    }
                }
                span {
                    font-size:13px; color:$gray7; font-weight:$medium; line-height:36px; padding:0 16px 0 0; position:relative;
                    @include transition(color, .2s);
                    &:after {
                        width:8px; height:12px; background-image:url(../../images/my_menu/order_arr_ico.png); content:""; position:absolute; top:50%; right:0;
                        @include translate(0, -50%);
                        @include transition(background-image, .2s);
                    }
                }
            }
        }
        .form_btn_wrap {padding:40px 0 0;}
    }
    // 이미지 없는 버전
    .prd_info_type01 {
        .prd_item {
            margin:40px 0 0; border-top:1px solid $gray2; padding:40px 0 0;
            &:first-child {margin:0; border:none; padding:0;}
            .info {
                &:after {display:block; clear:both; content:"";}
                .info_title {
                    width:450px; float:left;
                    strong {font-size:28px; display:block; color:$gray7; font-weight:$bold;}
                    em {font-size:13px; padding:7px 12px; border-radius:20px; margin:15px 0 0; display:inline-block; color:$gray5; font-weight:$medium; background-color:$gray1;}
                }
                .ordered_info_wrap {
                    width:calc(100% - 450px); float:left;
                    .ordered_info {
                        font-size:24px; line-height:28px; text-align:right; color:$gray7; font-weight:$medium; display:flex;
                        em {width:20%;}
                        span {width:35%;}
                        strong {width:45%;}
                    }
                    .ordered_history {display:none;}
                    .order_total_price_m {display:none;}
                }
            }
            .option_list {
                width:100%; padding:66px 0 0;
                &.discount {padding:40px 0 0;}
                > li {
                    padding:0 12px 0 3px; border-bottom:1px solid $gray2; position:relative;
                    &.border_top {border-top:1px solid $gray2;}
                    > span {font-size:16px; color:$gray7; font-weight:$medium; position:absolute; top:20px; right:46px;}
                }
                button {
                    width:100%; text-align:left; font-size:16px; color:$gray5; font-weight:$medium; position:relative; padding:22px 0;
                    &.active {
                        &:after {transform:rotate(180deg) translateY(50%);}
                    }
                    &.disabled {
                        transition:none;
                        &:after {opacity:.4; transform:rotate(0) translateY(-50%);}
                    }
                    &:after {
                        width:13px; height:8px; content:""; background-image:url(../../images/my_menu/order_more_btn.png); position:absolute; top:50%; right:0;
                        @include translate(0, -50%);
                        @include transition(transform, .2s);
                    }
                }
                .detail_option {
                    margin:0 0 20px; padding:40px; display:none; background-color:$gray1;
                    .first_option, .second_option {
                        @extend %clearfix;
                    }
                    .first_option {margin:0 0 36px;}
                    .second_option {
                        .product_area {padding:40px 0 0; border-top:1px solid #dcdcdc;}
                        .product_info_area {
                            padding:40px 0 0 80px; border-top:1px solid #dcdcdc;
                            span {padding:54px 0 0;}
                        }
                    }
                    p {font-size:16px; color:$gray7; font-weight:$medium;}
                    .product_area {
                        width:240px; float:left;
                        ul {padding:38px 0 0;}
                        li {
                            margin:12px 0 0; padding:0 0 0 12px; font-size:14px; color:$gray5; position:relative;
                            &:first-child {margin:0;}
                            &:before {
                                content:"·"; position:absolute; top:50%; left:0;
                                @include translate(0, -50%);
                            }
                        }
