                    }
                    &:first-of-type { width:100%; padding:em(42) em(28); margin:0;}
                    &:before {display:none;}
                }
            }
            .total_price {
                float:inherit; display:block; padding:em(20) em(30) em(20);
                .price_p {font-size:em(52); float:right;}
                li {
                    width:auto; font-size:em(52); line-height:em(52); font-weight:$bold;padding:0;
                    text-align:right; margin:0;
                    .option_txt {
                        padding:0 em(44) 0 0; position:absolute; top:0; left:0; font-size:0;
                        &:before {content:"Total"; text-indent:0; font-size:em(32);}
                        &:after {
                            width:em(24); height:em(15); position:absolute; top:30%; right:0; content:""; background-image:url(../../images/common/models_arr.png); opacity:.6;
                            @include backgroundSoltC();
                            @include translate(0, -50%);
                            @include rotate(0);
                            @include transition();
                        }
                        &.on:after {
                            @include rotate(180);
                        }
                    }
                    &:before {display:none;}
                }
            }
            .info_btn {
                padding:0 em(30) em(30); font-size:0;
                li {
                    display:inline-block; vertical-align:middle; margin:0 em(8) 0 0;
                    &:last-child {margin:0;}
                    // 비활성화 상태
                    &.btn_disabled {
                        &:hover, &:focus {
                            button, a {color:#b8b8b9;}
                        }
                        button, a {border:1px solid #b8b8b9;background-color:$white;cursor:default;color:#b8b8b9;}
                    }
                    &.download_btn.btn_disabled {
                        display:none;
                        &:before {
                            background:url(./../../images/design_order/step_bar_download_icon_disabled.png) 50% 50% no-repeat;background-size:cover;
                        }
                        &:hover, &:focus {
                            &:before {
                                //background:url(./../../images/design_order/step_bar_download_icon_over.png) 50% 50% no-repeat;background-size:cover;
                            }
                        }
                    }
                    &.wish_btn.btn_disabled {
                        &:before {
                            background-image:url(./../../images/design_order/step_bar_heart_icon_disabled_m.png);
                        }
                        &:hover, &:focus {
                            &:before {
                                //background:url(./../../images/design_order/step_bar_heart_icon_over.png) 50% 50% no-repeat;background-size:cover;
                            }
                        }
                    }
                    &.cart_btn.btn_disabled {
                        &:before {
                            background-image:url(./../../images/design_order/step_bar_cart_icon_disabled_m.png);
                        }
                    }
                    &.next_step_btn.btn_disabled {
                        a {
                            background-color:#b0cff9;cursor:inherit;color:$white;border-color:#b0cff9;
                            &:hover, &:focus {background-color:#b0cff9;color:$white;}
                        }
                    }
                }
                // 활성화
                a, button {@include borderRadius(6rem);}
                .download_btn, .wish_btn {
                    width:em(90); height:em(90);
                    &:before {
                        width:em(51); height:em(50); content:"";display:block;position:absolute; top:50%;left:50%;
                        pointer-events:none;
                        @include transform(translate(-50%, -50%));
                        @include transition(all, .2s);
                    }
                }
                .cart_btn, .next_step_btn {
                    width:auto;
                    button, a {font-size:em(32); font-weight:$bold;}
                }
                // 개별
                .download_btn {
                    display:none;
                    &:before {
                        background:url(./../../images/design_order/step_bar_download_icon.png) 50% 50% no-repeat;background-size:cover;
                    }
                    &:hover, &:focus {
                        &:before {
                            background:url(./../../images/design_order/step_bar_download_icon_over.png) 50% 50% no-repeat;background-size:cover;
                        }
                    }
                }
                .wish_btn {
                    display:inline-block; vertical-align:middle;
                    &:before {
                        background-image:url(./../../images/design_order/step_bar_heart_icon_m.png);
                    }
                    &:hover, &:focus {
                        &:before {
                            background-image:url(./../../images/design_order/step_bar_heart_icon_over_m.png);
                        }
                    }
                }
                .cart_btn {
                    width:em(90); height:em(90); display:inline-block; vertical-align:middle;position:relative;
                    button {text-indent:-9999px;}
                    &:before {
                        width:em(51); height:em(50); content:"";display:block;position:absolute; top:50%;left:50%;
                        pointer-events:none;
                        background:url(./../../images/design_order/step_bar_cart_icon_m.png) no-repeat 50% 50%; background-size:cover;
                        @include transform(translate(-50%, -50%));
                        @include transition(all, .2s);
                    }
                    &:hover, &:focus {
                        button {color:$blue5;}
                    }
                }
                .next_step_btn {
                    display:inline-block; vertical-align:middle; width:calc(100% - 9rem); height:inherit;
                    &:hover, &:focus {
                        button { background-color:#045fd5; }
                    }
                    button {
                        box-shadow:none; line-height:em(90);
                        @include borderRadius(0.5rem);
                    }
                }
            }
        }
        // 옵션 바
        #optionBar {display:none;}
        // 옵션 리스트
        .option_wrap {
            padding:em(90) 0 0;
            > .scroll_wrap {
                padding:0; height:auto;
                .item_list {
                    padding:em(40) 0 0;
                    > p {font-size:em(36); font-weight:$bold; padding:0 0 em(20);}
                }
            }
            // 두줄
            &.option_list {
                .sub_desc {width:100%;margin:0;display:block;padding:0; border-top:em(2) solid $gray2;}
                .column_list_option {
                    .list_elm {
                        margin:35px 0 0; border-top:em(2) solid $gray2; padding:30px 0 0;
                    }
                }
                .in_wrap {
                    > div {
                        display:block;border-top:em(2) solid $gray2;margin:0;padding:0;
                        &:nth-of-type(n+2) {
                            .row_list_option {padding:em(56) em(58) em(54);}
                        }
                    }
                }
            }
            .in_wrap {
                padding:0; margin:0; border:em(2) solid $gray3; overflow:hidden; display:block;
                @include borderRadius(em(16));
            }
            .column_list_option {
                width:100%; padding:em(44) em(58) em(40);
                > div {
                    position:relative; border-top:em(2) solid $gray2; margin:em(30) 0 0; padding:em(40) 0 0 em(200);
                    &:first-of-type {border:none; padding:0;}
                }
                .split_list {
                    display:block;
                    ul { width:auto; }
                }
                span {font-size:em(32); line-height:em(36); position:absolute;}
                ul {padding:0 0 0 em(200);}
                li {font-size:em(28); padding:0 0 0 em(20); line-height:em(36);}
            }
            .row_list_option {
                width:100%; background-color:#fafafa; padding:em(38) em(58) em(32); box-sizing:border-box;
                border-top:em(2) solid $gray2; margin:0;
                &:nth-of-type(2) {border:0; margin:0; padding:em(56) em(58) em(54);}
                ul {display:block;}
                li {
                    margin:em(30) 0 0; padding:0;
                    &:nth-of-type(1) { width:auto; }
                    &:nth-of-type(2) { width:auto; }
                    &:nth-of-type(3) { width:auto; }
                    &:nth-of-type(4) { width:auto; }
                    &:first-child {margin:0;}
                    &:after {content:""; clear:both; display:block;}
                }
                .last_price_txt {
                    max-width:100%;
                    p {font-size:em(36); line-height:em(46); padding:0;}
                }
                span {font-size:em(28); color:$gray6; line-height:em(36); float:left;}
                p {padding:0; font-size:em(28); float:right;}
            }
        }
    }
}
