        }
        .btn_wrap {
            margin:55px 0 0; overflow:hidden;
            a, button {
                float:left; width:calc(50% - 2px); display:block;
                &:last-child {float:right;}
            }
            .btn_type01 {
                font-size:18px; line-height:58px; font-weight:$medium; background-color:$white; border:1px solid $gray3; border-radius:10px; display:block; box-sizing:border-box; color:$gray7;
                @include transition();
                &:hover, &:focus {
                    color:$white; background-color:$gray7; border-color:$gray7;
                }
            }
            .btn_type02 {
                font-size:18px; line-height:58px; font-weight:$medium; background-color:$blue5; border:1px solid $blue5; border-radius:10px; display:block; box-sizing:border-box; color:$white; cursor:pointer;
                @include transition();
                &:hover, &:focus {
                    background-color:$blue6; border-color:$blue6;
                }
                &.btn_disabled {background-color:$blue4; border-color:$blue4; cursor:default;
                    &:hover, &:focus {
                        background-color:$blue4; border-color:$blue4;
                    }
                }
            }
        }
    }
    .btn_popup_close {
        width:20px; height:20px; top:28px; right:26px; background-image:url(../../images/common/close_x_btn.png);
    }
    @include mob() {
        .popup_content {
            max-width:100%; margin:0 em(30); padding:em(100) em(40) em(40); border-radius:em(24);
            .title {
                margin:0 em(50);
                strong {font-size:em(48); font-weight:$bold;}
            }
            .txt {
                p {
                    font-size:em(32); padding:em(274) 0 0; color:$gray5;
                    &:before {width:em(276); height:em(268); background-size:em(276) em(268);}
                }
            }
            .btn_wrap {
                margin:em(58) 0 0;
                a, button {width:calc(50% - 4px);}
                .btn_type01 {font-size:em(24); line-height:em(80); border:em(2) solid $gray3; border-radius:em(8);}
                .btn_type02 {font-size:em(24); line-height:em(80); border:em(2) solid $blue5; border-radius:em(8);}
            }
        }
        .btn_popup_close {
            width:em(72); height:em(72); top:em(22); right:em(22); background-image:url(../../images/common/close_x_btn_m.png);
        }
    }
}