@charset "UTF-8";

.member_container {
  .btn_type01 {
    font-size:18px; line-height:58px; font-weight:$medium; background-color:$white; border:1px solid $gray3; border-radius:10px; display:block; box-sizing:border-box; color:$gray7;
    @include transition();
    &:hover, &:focus {
      color:$white; background-color:$gray7; border-color:$gray7;
    }
  }
  .btn_type02 {
    font-size:18px; line-height:58px; font-weight:$medium; background-color:$blue5; border:1px solid $blue5; border-radius:10px; display:block; box-sizing:border-box; color:$white; cursor:pointer;
    @include transition();
    &:hover, &:focus {
      background-color:$blue6; border-color:$blue6;
    }
    &.btn_disabled {background-color:$blue4; border-color:$blue4; cursor:default;
      &:hover, &:focus {
        background-color:$blue4; border-color:$blue4;
      }
    }
  }

  .member_default {
    max-width:460px; margin:0 auto; padding:100px  40px; box-sizing:border-box;
    .login_form {
      .form_list {
        li {
          margin:8px 0 0;
          &:first-child {margin:0;}
          .form_text_type01 label.required:before {display:none;}
        }
      }
      .form_check_radio_default {margin:25px 0 20px}
      .form_check_radio_default input[type="checkbox"] + label {font-size:14px; color:$gray5; padding:0 0 0 30px;}
      input[type=submit], button {width:100%;}
      .info {
        padding:15px 0 0; text-align:center;
        p {
          padding:0 0 40px;
          a {font-size:14px; text-decoration:underline; color:$gray6;}
        }
      }
    }

    .infoFind_form {
      ul {margin:0 0 60px;}
      li {
        margin:8px 0 0;
        &:first-child {margin:0;}
        .form_text_type01 label.required:before {display:none;}
      }
      input[type=submit], button {width:100%;}
      .info {
        font-size:14px; line-height:22px; padding:0 0 50px; color:$gray5; text-align:center;
        span {
          position:relative; font-weight:$medium;
          &:after {width:calc(100% + 6px); height:10px; background-color:#e6f1ff; position:absolute; bottom:0; left:-3px; z-index:-1; content:"";}
        }
      }
    }

    .infoFind_ok {
      li {
        margin:8px 0 0;
        &:first-child {margin:0;}
      }
      .info {
        text-align:center;
        strong {font-size:24px; line-height:34px; font-weight:$bold; color:$gray7; display:block;}
        p {
          font-size:14px; line-height:22px; color:$gray5; padding:35px 0 50px;
          span {
            position:relative; font-weight:$medium;
            &:after {width:calc(100% + 10px); height:10px; background-color:#e6f1ff; position:absolute; bottom:0; left:-5px; z-index:-1; content:"";}
          }
        }
      }
    }
  }

  .member_signUp {
    max-width:460px; margin:0 auto; padding:60px 40px; box-sizing:border-box;
    .signUp_form {
      .form_list {
        margin:0 0 30px;
        li {
          margin:8px 0 0;
          &:first-child {margin:0;}
          .form_text_type01 label.required:before {display:none;}
        }
      }
      .terms_wrap {
        margin:15px 0 30px; padding:20px; background-color:$gray1; box-sizing:border-box; border-radius:8px;
        li {
          margin:6px 0 0; position:relative;
          &:first-child {margin:0;}
          .form_check_radio_default {
            display:inline-block;
            input[type="checkbox"] + label {padding:0 0 0 26px; font-size:13px; line-height:18px; color:$gray5;}
            input[type="checkbox"] + label:before {width:18px; height:18px; border-color:$gray3; border-radius:3px;}
            input[type="checkbox"] + label:after {width:18px; height:18px;}

            input[type="checkbox"]:hover ~ label:before {border-color:$blue5 !important;}
            input[type="checkbox"]:focus ~ label:before {border-color:$blue5;}
          }
          button {
            width:calc(100% - 26px); height:100%; display:block; position:absolute; text-indent:-9999px; top:2px; right:0;
            &:after {
              width:12px; height:12px; position:absolute; top:50%; right:0; content:""; background-image:url(../../images/common/more_arr.png);
              @include translate(0, -50%);
              @include backgroundSoltC();
            }
          }
        }
      }
      input[type=submit], button {width:100%;}
    }
  }

  .terms_popup_01, .terms_popup_02 {
    .popup_content {
      max-width:460px; padding:40px; box-sizing:border-box; background-color:$white; border-radius:20px;
      > strong {font-size:26px; line-height:36px; text-align:center; font-weight:$medium; color:$gray7; display:block; padding:0 20px;}
      .terms_cont {
        height:400px; margin:30px 0 0; overflow-y:auto;
        .title {
          border-bottom:1px solid $gray2; padding:0 0 30px;
          strong {font-size:16px; font-weight:$medium; display:block;}
          span {font-size:14px; color:$gray5}
        }
        .cont {
          color:$gray5; padding:30px 0 0;
          > strong {font-size:16px; margin:0 0 10px; display:block;}
          > p {font-size:14px; line-height:22px;}
        }
      }
    }
    .btn_popup_close {
      width:24px; height:24px; text-indent:-9999px; position:absolute; top:20px; right:20px;
      &:after {
        background-image:url(../../images/common/close_x_btn.png); content:"";
        @include posCenterSolt();
        @include translate(-50%, -50%);
        @include textBackground(12px, 12px);
        @include backgroundSoltC();
      }
    }
  }

  @include mob() {
    .btn_type01 {font-size:em(32); line-height:em(116); font-weight:$bold; border:em(2) solid $gray3; border-radius:em(8);}
    .btn_type02 {font-size:em(32); line-height:em(116); font-weight:$bold; border:em(2) solid $blue5; border-radius:em(8);}

    .member_default {
      max-width:100%; padding:em(100) em(30);
      .login_form {
        .form_list {
          li {margin:em(12) 0 0;}
        }
        .form_check_radio_default {margin:em(70) 0 em(50);}
        .form_check_radio_default input[type="checkbox"] + label {font-size:em(28); padding:0 0 0 em(56);}
        .info {
          padding:em(36) 0 0;
          p {
            padding:0 0 em(86);
            a {font-size:em(28); text-underline-position:under;}
          }
        }
      }

      .infoFind_form {
        ul {margin:0 0 em(90);}
        li {margin:em(12) 0 0;}
        .info {
          font-size:em(32); line-height:em(44); padding:0 0 em(88); font-weight:$medium;
          span {
            font-weight:$bold;
            &:after {height:em(20); left:em(-5);}
          }
        }
      }

      .infoFind_ok {
        li {margin:em(12) 0 0;}
        .info {
          strong {font-size:em(48); line-height:em(60);}
          p {
            max-width:em(500); margin:0 auto; font-size:em(32); line-height:em(44); padding:em(62) 0 em(74);
            span {
              font-weight:$bold;
              &:after {width:calc(100% + 4px); height:em(20); left:em(-4);}
            }
          }
        }
      }
    }

    .member_signUp {
      max-width:100%; padding:em(100) em(30);
      .signUp_form {
        .form_list {
          margin:0 0 em(60);
          li {margin:em(12) 0 0;}
        }
        .terms_wrap {
          margin:em(24) 0 em(60); padding:em(32); border-radius:em(16);
          li {
            margin:em(10) 0 0;
            .form_check_radio_default {
              input[type="checkbox"] + label {padding:0 0 0 em(46); font-size:em(26); line-height:em(36);}
              input[type="checkbox"] + label:before {width:em(36); height:em(36); border:em(2) solid $gray3; border-radius:em(8);}
              input[type="checkbox"] + label:after {width:em(36); height:em(36);}
            }
            button {
              width:calc(100% - 40px); top:em(2);
              &:after {width:em(36); height:em(36); background-image:url(../../images/common/more_arr_m.png);}
            }
          }
        }
      }
    }

    .terms_popup_01, .terms_popup_02 {
      .popup_content {
        max-width:100%; margin:em(30); padding:em(80) em(30); border-radius:em(24);
        > strong {font-size:em(48); line-height:em(60); font-weight:$bold; color:$gray7; padding:0;}
        .terms_cont {
          height:em(800); margin:em(70) 0 0;
          .title {
            border-bottom:em(2) solid $gray2; padding:0 0 em(48);
            strong {font-size:em(36); font-weight:$bold; padding:0 0 em(12);}
            span {font-size:em(32); font-weight:$medium;}
          }
          .cont {
            padding:em(40) 0 0;
            > strong {font-size:em(36); line-height:em(48); margin:0 0 em(24); font-weight:$medium;}
            > p {font-size:em(32); line-height:em(48); font-weight:$medium;}
          }
        }
      }
      .btn_popup_close {
        width:em(72); height:em(72); top:em(22); right:em(22);
        &:after {
          width:em(72); height:em(72); background-size:em(72) em(72); background-image:url(../../images/common/close_x_btn_m.png);
        }
      }
    }
  }
}